@import url(https://fonts.googleapis.com/css?family=Lato:300,400,600);

/* ---------------------------------------------------
    CSS VARIABLES
--------------------------------------------------- */
:root {
  --primary-color: #06CACB;
}

/* ---------------------------------------------------
    BASE STYLES
--------------------------------------------------- */

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

body {
  font-family: "Lato", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;

  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(225deg,
    #06CACB 0%, #06CACB 30%,
    #DDE5EE 30%, #DDE5EE 75%,
    #F5F7FA 75%, #F5F7FA 100%
  );
  background-image: linear-gradient(225deg,
    var(--primary-color) 0%, var(--primary-color) 30%,
    #DDE5EE 30%, #DDE5EE 75%,
    #F5F7FA 75%, #F5F7FA 100%
  );
  overflow-y: hidden;
}

input {
  border: none;
}
input:focus {
  outline: none;
  border-bottom: none;
}

/* FIXES WEIRD SAFARI GLITCH */
input { -webkit-user-select: text; }


@media only screen and (max-width: 700px) {
  body {
    background-image: none;
  }
}
.App {
  width: 900px;
  height: 80vh;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  display: flex;
  box-shadow: 0 0 25px rgba(0,0,0,.25);
}

/* RESPONSIVE WIDTHS */
@media only screen and (max-width: 1000px) {
  .App {
    width: 700px;
  }
}
@media only screen and (max-width: 700px) {
  .App {
    width: 100vw;
    height: 100vh;
    flex-direction: column;
  }
}
@media only screen and (max-width: 350px) {
  
}

/* RESPONSIVE HEIGHTS */
@media only screen and (max-height: 400px) {
  .App {
    height: 100vh;
  }
}
.Welcome.hidden {
  -webkit-animation: disappear 1s forwards;
          animation: disappear 1s forwards;
}
.Welcome {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--primary-color);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Welcome .modal {
  width: 200px;
  padding: 75px 100px;
  background: #fff;
  text-align: center;
  border-radius: 5px;
  position: relative;
  box-shadow: 0 15px 35px rgba(0,0,0,.3);
}
.Welcome h2 {
  font-size: 40px;
  font-weight: 300;
  color: #888;
  margin-bottom: 5px;
}
.Welcome h2 span {
  font-weight: 600;
  color: #444;
}
.Welcome p {
  font-size: 13px;
}
.Welcome .info p {
  margin-top: 15px;
}
.Welcome form {
  margin-top: 15px;
}
.Welcome form .username {
  width: 100%;
  font-size: 13px;
  font-weight: 300;
  color: #444;
  padding: 10px;
  border-bottom: 1px solid rgba(127,127,127,.2);
  transition: border .25s ease-in;
}
.Welcome form .username:focus {
  border-bottom: 1px solid var(--primary-color);
}
.Welcome form button {
  font-size: 13px;
  padding: 10px 20px;
  color: #fff;
  background: var(--primary-color);
}
.Sidebar {
  box-sizing: border-box;
  height: 100%;
  width: 250px;
  padding: 20px 40px;;
  background: #fff;
  box-shadow: 4px 0 4px rgba(0,0,0,.1);
  z-index: 1;
}
.Sidebar .heading h2 {
  font-size: 32px;
  font-weight: 300;
  text-align: center;
  color: #888;
  margin: 20px 0 0 0;
}
.Sidebar .heading h2 span {
  font-weight: 600;
  color: #444;
}
.Sidebar .heading .subtitle {
  margin-top: 0;
  text-align: center;
  font-size: 12px;
  color: #444;
}

.Sidebar .UserList {
  height: 300px;
  margin-top: 40px;
  overflow-y: auto;
}
.Sidebar .UserList h3 {
  font-weight: 400;
  font-size: 12px;
  text-transform: uppercase;
  color: #888;
}
.Sidebar .UserList .User {
  margin: 7px 0;
  padding-left: 15px;
  font-size: 12px;
  font-weight: 600;
  color: #888;
  position: relative;
}

